<template>
  <div class="quarter" 
  :style="{backgroundColor: params.backgroundColor}"
  :class="{quarter__active: isActive}"
  >
  </div>
</template>

<script>
import { eventBus } from "@/main.js";

export default {
  name: "Quarter",
  props: {
    params: {
      type: Object
    }
  },
  data(){
    return {
      isActive: false
    }
  },
  created(){
    let sound = new Audio(this.params.soundPath);

    eventBus.$on('turnQuarter', (id) => {

      if(this.params.id === id){
        //Подсвечиваем четвертинку
        this.isActive = true;
        sound.play();
        //удаляем подсветку по завершению аудио дорожки
        sound.addEventListener("ended", () => {
          this.isActive = false;
        });
      }

    });
  }
}
</script>

<style>
.quarter{
  width: 50%;
  height: 50%;
  box-sizing: border-box;
  opacity: .6;
}
.quarter:hover{
  cursor: pointer;
  opacity: .8;
}
.quarter:active, .quarter__active{
  opacity: 1;
}
.quarter__active{
  box-shadow: inset 0px 0px 50px rgba(0, 0, 0, .5);
}
</style>