<template>
  <div id="app">
    <h1>Simon the game</h1>
    <div class="game">
      <div class="game__circle">
        <Quarter v-for="item in quarters" 
                :key="item['id']" 
                :params="item" 
                @click.native="clickQuarter(item['id'])"
                />
      </div>
      <div class="game__control-elements">
        <h2>Round: {{ series.length }}</h2>
        <div class="level">
          <h3>Level:</h3>
          <select class="level__select" v-model="currentLevel">
            <option v-for="(level, index) in levels" :key="index" :value="level">{{ level }}</option>
          </select>
        </div>
        <div class="button" @click="startGame">Start</div>
        <p class="game__lose-massage">{{ loseMessage }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { eventBus } from "@/main.js";
import Quarter from "@/components/Quarter.vue";
import quarterProps from "@/common/quarter-props.js";

export default {
  name: 'App',
  data(){
    return {
      quarters: quarterProps,
      userSeries: [],
      series: [],
      seriesId: null,
      gameStarted: false,
      levels: ["easy", "normal", "hard"],
      currentLevel: "easy",
      delay: null,
      loseMessage: ""
    }
  },

  methods:{
    turnQuarter(id) {
      if(this.gameStarted)
        eventBus.$emit('turnQuarter', id);
    },

    clickQuarter(id) {
      if(this.gameStarted){

        //Ничего не далать если проигрывание серии не закончилось
        if(this.seriesId !== null) return;

        this.turnQuarter(id);
        this.userSeries.push(id);
        let currentIndex = this.userSeries.length - 1;

        //Проигрыш
        if(this.series[currentIndex] !== this.userSeries[currentIndex]) {
          this.loseMessage = "You lost the game";
          this.resetGame();
          return;
        }
        
        //Пользователь закончил вводить серию и не ошибся
        if(this.series.length === this.userSeries.length) {
          //Обнуляем пользовательскую серию для корректной проверки выше
          this.userSeries = [];
          this.setSeries();
          this.runSeries();
        }
        
      }
    },

    checkSeries() {
      //Конвертируем массивы в строку и сравниваем их
      let seriesArrayToString = this.series.toString();
      let userSeriesArratToString = this.userSeries.toString();
      return seriesArrayToString === userSeriesArratToString;
    },

    startGame() {
      if(!this.gameStarted) {
        this.gameStarted = true;
        this.loseMessage = "";
        this.setDelay();
        this.setSeries();
        this.runSeries();
      } else {
        this.resetGame();
        this.startGame();
      }
    },

    setDelay() {
      switch(this.currentLevel) {
        case "easy":
          this.delay = 1500;
          break;
        case "normal":
          this.delay = 1000;
          break;
        case "hard":
          this.delay = 400;
          break;
        default:
          this.belay = 1500;
      }
    },

    setSeries() {
      //Получаем случайное целое число от 0 и до 3
      let elem = Math.floor( Math.random() * this.quarters.length );
      this.series.push(elem);
    },

    runSeries() {
      let currentItem = 0;
      //Устанавливаем интервал на заданную задержку
      //Id интервала записываем в this.seriesID
      this.seriesId = setInterval( () => {
        //Активируем текущую четвертинку
        this.turnQuarter(this.series[currentItem]);

        currentItem++;

        //Серия завершилась
        if(this.series.length <= currentItem)
          this.stopSeries();
      }, this.delay)
    },

    stopSeries() {
      clearInterval(this.seriesId);
      this.seriesId = null;
    },

    resetGame() {
      this.stopSeries();
      this.userSeries = [];
      this.series = [];
      this.gameStarted = false;
    }
  },
  components: {
    Quarter
  }
}
</script>

